body {
  margin: 0px;
  font-family: sans-serif;
  -webkit-font-smoothing: antialiased;
}

.hero {
  max-width: 100vw;
  height: 100vh;

  display: flex;
  flex-direction: column;

  background-image: linear-gradient(135deg, rgba(51,0,131,0.6) 32%, rgba(252,0,255,0.6) 100%), url(../../images/background.jpg);
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}

.hero-inner {
  margin: 30px 0;
  color: #fff;
  text-align: center;
  justify-content: center;
  align-items: center;
  text-shadow: 2px 2px 2px black;
}

#resume {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-size: 1em;
  text-align: left;
}

#resume>ul {
  list-style-type: square;
  list-style-position: inside;
  background-color: #6c0092;
  border: 3px solid #fff;
  border-radius: 20px;
  text-shadow: 1px 1px 2px black;
}

#resume>ul>li {
  margin: 10px;
}


[hidden] {
  display: none !important;
}

h1 {
  font-size: 2em;
  margin: 20px;
}

h2 {
  font-size: 1.5em;
}

.buttons {
  display: flex;
  justify-content: center;
}

.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  max-width: 120px;
  width: auto;
  background-color: #fff;
  padding: 1em;
  margin: 20px;
  
  color: #330083;
  text-decoration: none;
  font-size: 1em;
  text-align: center;
  font-weight: bold;
  
  border: 3px solid #330083;
  border-radius: 20px;
}

.btn:hover {
  background-color: #6c0092;
  color: #fff;
  border: 3px solid #fff;
}
