.App {
    display: flex;
    justify-content: center;
    align-items: center;
    }
     
    .weather {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 20px;
    padding: 20px 0;
    border-radius: 10px;
    width: 150px;
    height: 150px;
    color: #fff;
    text-align: center;
    text-shadow: 2px 2px 2px black;
    }
     
    .weather-location {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 10px;
    }
     
    .weather-date {
    font-size: 16px;
    margin-bottom: 10px;
    }
     
    .weather-temperature {
    font-size: 36px;
    font-weight: bold;
    margin-bottom: 10px;
    }
         
    .weather-description {
    font-size: 16px;
    }

    .photo {
        margin: 20px;
        border-radius: 50%;
        width: 200px;
        height: 200px;
      }
     
    @media screen and (max-width: 600px) {
        .photo {
            width: 120px;
            height: 120px;
            align-items: center;
        }
    }